import React, { useState } from "react";
import { Form, Checkbox, message, Popover } from "antd";
import styled from "styled-components";
import { useHistory } from "react-router-dom";
import { connect } from "react-redux";
// import { v4 as uuid } from "uuid";
// import * as R from "ramda";
import { withNamespaces } from "react-i18next";
// import jwtDecode from "jwt-decode";

import AuthForm from "./AuthForm";
import RegistrationForm from "./RegistrationForm";
import ConfirmEmailModal from "./ConfirmEmailModal";
import { AuthSuccessActionCreator } from "../../../redux/actionCreators/AuthActionCreators";
// import axiosInstance from "../../../axios/axiosInstance";
import {
  EnterForm,
  EnterButton,
  ButtonWrapper,
} from "../../../common/StyledComponents";
import PolicyPDF from "../../../assets/policy.pdf";

const MainForm = ({ form, authSuccess, t }) => {
  const history = useHistory();
  const [isUserNew, setUserNew] = useState(false);
  const [conditionsChecked, setConditionsChecked] = useState(false);
  const [isConfirmEmailModalVisible, setConfirmEmailModalVisible] = useState(
    false
  );
  const [email, setEmail] = useState("");

  const {
    enter,
    acceptPolitics,
    registration,
    policyValidator,
    forgetPassword,
    checkEmail,
    enterEmail,
    openPolicyPDF,
  } = t("auth", {
    returnObjects: true,
  });

  const handleSubmit = (e) => {
    e.preventDefault();
    form.validateFields(async (err, values) => {
      const { name, email } = values;
      if (!err && isUserNew) {
        if (conditionsChecked) {
          await setEmail(values.email);
          const [first_name, last_name] = name.split(" ");
          authSuccess({ first_name, last_name, email, name });
          history.push("/");
        } else {
          message.error(policyValidator);
        }
      } else if (!err && !isUserNew) {
        authSuccess({ email, name: "Mocked Name" });
        history.push("/");
      }
    });
  };
  const {
    getFieldDecorator,
    validateFields,
    getFieldValue,
    resetFields,
  } = form;

  const onChange = (e) => {
    setConditionsChecked(e.target.checked);
  };

  const handleResetPassword = (e) => {
    if (!e.keyCode === 13) {
      const email = getFieldValue("email");
      !email ? message.error(enterEmail, 5) : message.info(checkEmail, 5);
    }
  };

  const content = (
    <a href={PolicyPDF} target="_blank" rel="noopener noreferrer">
      {openPolicyPDF}
    </a>
  );

  const handleOpenPolicy = () => {};

  return (
    <>
      <EnterForm>
        {isUserNew ? (
          <>
            <RegistrationForm
              getFieldDecorator={getFieldDecorator}
              getFieldValue={getFieldValue}
              validateFields={validateFields}
            />
            <Popover content={content} placement="right">
              <CircleCheckbox onChange={onChange} onClick={handleOpenPolicy}>
                {acceptPolitics}
              </CircleCheckbox>
            </Popover>
          </>
        ) : (
          <>
            <AuthForm getFieldDecorator={getFieldDecorator} />
            <ForgetPasswordButton onClick={handleResetPassword}>
              {forgetPassword}
            </ForgetPasswordButton>
          </>
        )}
        <ButtonWrapper>
          <EnterButton
            type={!isUserNew ? "primary" : ""}
            onClick={(e) => {
              if (isUserNew) {
                resetFields();
                setUserNew(false);
              } else {
                handleSubmit(e);
              }
            }}
          >
            {enter}
          </EnterButton>
          <EnterButton
            type={isUserNew ? "primary" : ""}
            onClick={(e) => {
              if (isUserNew) {
                handleSubmit(e);
              } else {
                setUserNew(true);
                resetFields();
              }
            }}
          >
            {registration}
          </EnterButton>
        </ButtonWrapper>
      </EnterForm>
      <ConfirmEmailModal
        email={email}
        isConfirmEmailModalVisible={isConfirmEmailModalVisible}
        setConfirmEmailModalVisible={setConfirmEmailModalVisible}
      />
    </>
  );
};

const CircleCheckbox = styled(Checkbox)`
  color: #fff;
  margin: 5px 0 15px 0;
  .ant-checkbox-inner {
    border-radius: 14px;
    background-color: #fff;
    border-color: #d2d8dd;
    position: relative;
    ::after {
      content: "";
      width: 8px;
      height: 8px;
      background-color: #f85c71;
      border: none;
      border-radius: 8px;
      transform: none;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
  .ant-checkbox-checked {
    ::after {
      display: none;
    }
  }
  .ant-checkbox-input:focus + .ant-checkbox-inner {
    border: none;
  }
  span {
    font-size: 13px;
    line-height: 20px;
    font-weight: 200;
  }
`;

const ForgetPasswordButton = styled.button`
  display: block;
  cursor: pointer;
  border: none;
  outline: none;
  background: transparent;
  font-size: 15px;
  text-align: left;
  margin: 15px 10px;
  color: #ffffff;
`;

const WrappedRegistrationForm = Form.create({ name: "normal_login" })(MainForm);

const mapDispatchToProps = (dispatch) => ({
  authSuccess: (userData) => dispatch(AuthSuccessActionCreator(userData)),
});

export default withNamespaces()(
  connect(null, mapDispatchToProps)(WrappedRegistrationForm)
);
