import React from "react";
import { withNamespaces } from "react-i18next";
import { useHistory } from "react-router-dom";

import {
  RegularText,
  CenterAlignModal,
  CancelButton,
} from "../../../common/StyledComponents";
import { message } from "antd";

const NewPasswordSavedModal = ({ isModalVisible, setModalVisible, t }) => {
  const history = useHistory();
  const { newPasswordSaved, ready, successReset } = t("auth", {
    returnObjects: true,
  });
  const handleReady = () => {
    history.push("/");
    message.success(successReset);
  };

  return (
    <CenterAlignModal
      centered
      width={435}
      visible={isModalVisible}
      onOk={() => setModalVisible(false)}
      onCancel={() => setModalVisible(false)}
      footer={[]}
    >
      <RegularText>{newPasswordSaved}</RegularText>
      <CancelButton onClick={handleReady} style={{ marginTop: 30 }}>
        {ready}
      </CancelButton>
    </CenterAlignModal>
  );
};

export default withNamespaces()(NewPasswordSavedModal);
