import React from "react";
import styled from "styled-components";

import WhiteLogo from "../../assets/logol.png";
import RetinaWhiteLogo from "../../assets/logol@2x.png";
import ColoredLogo from "../../assets/logor.png";
import RetinaColoredLogo from "../../assets/logor@2x.png";

const ResponsiveLogo = () => (
  <MainLogo>
    <LimitImg
      src={WhiteLogo}
      srcSet={`${WhiteLogo},
    ${RetinaWhiteLogo} 2x`}
      alt="logo"
    />
    <LimitImg
      src={ColoredLogo}
      srcSet={`${ColoredLogo},
    ${RetinaColoredLogo} 2x`}
      alt="logo"
    />
  </MainLogo>
);

const LimitImg = styled.img`
  max-width: 140px;
`;

const MainLogo = styled.div`
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
`;

export default ResponsiveLogo;
