import React, { useState } from "react";
import { Form } from "antd";
import { withNamespaces } from "react-i18next";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
// import * as R from "ramda";

import {
  EnterForm,
  EnterButton,
  ButtonWrapper,
  FormInputForAuth,
} from "../../../common/StyledComponents";
// import axiosInstance from "../../../axios/axiosInstance";
import NewPasswordSavedModal from "./NewPasswordSavedModal";

const ResetForm = ({ form, t, hash }) => {
  const history = useHistory();
  const [passwordState, setPasswordState] = useState({
    confirmDirty: false,
    autoCompleteResult: [],
  });
  const [isPasswordSavedModalOpen, setPasswordSavedModalOpen] = useState(false);

  const {
    cancel,
    confirm,
    confirmNewPassword,
    requiredValidator,
    notLessValidator,
    notEqualPasswords,
    onlyDigits,
    enterNewPassword,
  } = t("auth", {
    returnObjects: true,
  });
  const { getFieldDecorator, getFieldValue, validateFields } = form;

  const handleConfirmBlur = (e) => {
    const { value } = e.target;
    setPasswordState({
      ...passwordState,
      confirmDirty: passwordState.confirmDirty || !!value,
    });
  };

  const compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== getFieldValue("newPassword")) {
      callback(notEqualPasswords);
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    if (value && passwordState.confirmDirty) {
      validateFields(["confirmNewPassword"], { force: true });
    }
    callback();
  };

  const handleResetPasswordConfirm = (e) => {
    e.preventDefault();
    validateFields((err, values) => {
      // const { newPassword } = values;
      if (!err) {

      }
    });
  };

  return (
    <>
      <EnterForm>
        <FormItem>
          {getFieldDecorator("newPassword", {
            rules: [
              {
                required: true,
                message: requiredValidator,
              },
              {
                validator: validateToNextPassword,
              },
              {
                min: 8,
                message: notLessValidator,
              },
              {
                pattern: /^(?!^\d+$)^.+$/,
                message: onlyDigits,
              },
            ],
          })(
            <FormInputForAuth type="password" placeholder={enterNewPassword} />
          )}
        </FormItem>
        <Form.Item>
          {getFieldDecorator("confirmNewPassword", {
            rules: [
              {
                required: true,
                message: requiredValidator,
              },
              {
                validator: compareToFirstPassword,
              },
            ],
          })(
            <FormInputForAuth
              type="password"
              placeholder={confirmNewPassword}
              onBlur={handleConfirmBlur}
            />
          )}
        </Form.Item>
        <ButtonWrapper style={{ marginTop: 15 }}>
          <EnterButton
            onClick={(e) => {
              handleResetPasswordConfirm(e);
            }}
          >
            {confirm}
          </EnterButton>
          <EnterButton type="primary" onClick={(e) => history.push("/")}>
            {cancel}
          </EnterButton>
        </ButtonWrapper>
      </EnterForm>
      <NewPasswordSavedModal
        isModalVisible={isPasswordSavedModalOpen}
        setModalVisible={setPasswordSavedModalOpen}
      />
    </>
  );
};

const FormItem = styled(Form.Item)`
  label {
    color: #fff;
  }
`;

const WrappedResetPasswordForm = Form.create({ name: "reset_password" })(
  ResetForm
);

export default withNamespaces()(WrappedResetPasswordForm);
