import React from "react";
import { Form } from "antd";
import { withNamespaces } from "react-i18next";

import { FormInputForAuth } from "../../../common/StyledComponents";

const AuthForm = ({ getFieldDecorator, t }) => {
  const {
    email,
    password,
    requiredValidator,
    emailNotValid,
    notLessValidator,
  } = t("auth", {
    returnObjects: true,
  });

  return (
    <>
      <Form.Item>
        {getFieldDecorator("email", {
          rules: [
            {
              required: true,
              message: requiredValidator,
            },
            {
              type: "email",
              message: emailNotValid,
            },
          ],
        })(<FormInputForAuth placeholder={email} />)}
      </Form.Item>
      <Form.Item>
        {getFieldDecorator("password", {
          rules: [
            {
              required: true,
              message: requiredValidator,
            },
            {
              min: 8,
              message: notLessValidator,
            },
          ],
        })(<FormInputForAuth type="password" placeholder={password} />)}
      </Form.Item>
    </>
  );
};

export default withNamespaces()(AuthForm);
