import React from "react";
import { withNamespaces } from "react-i18next";

import { RegularText, ThinRedText, CenterAlignModal } from "../../../common/StyledComponents";

const ConfirmEmailModal = ({
  email,
  isConfirmEmailModalVisible,
  setConfirmEmailModalVisible,
  t,
}) => {
  const { successfully, onEmail, aLetterWasSent } = t("auth", {
    returnObjects: true,
  });

  return (
    <CenterAlignModal
      centered
      width={435}
      visible={isConfirmEmailModalVisible}
      onOk={() => setConfirmEmailModalVisible(false)}
      onCancel={() => setConfirmEmailModalVisible(false)}
      footer={[]}
    >
      <RegularText>{successfully}</RegularText>
      <RegularText>
        {onEmail}
        <ThinRedText>{email}</ThinRedText>
        {aLetterWasSent}
      </RegularText>
    </CenterAlignModal>
  );
};

export default withNamespaces()(ConfirmEmailModal);
