import React, { useState } from "react";
import { Form } from "antd";
import { withNamespaces } from "react-i18next";

import { FormInputForAuth } from "../../../common/StyledComponents";

const RegistrationForm = ({
  getFieldDecorator,
  getFieldValue,
  validateFields,
  t,
}) => {
  const [passwordState, setPasswordState] = useState({
    confirmDirty: false,
    autoCompleteResult: [],
  });

  const {
    confirmPassword,
    notLessValidator,
    requiredValidator,
    emailNotValid,
    notEqualPasswords,
    password,
    username,
    email,
    unacceptable,
    fullnameValidation,
    onlyDigits,
    maximum
  } = t("auth", {
    returnObjects: true,
  });

  const handleConfirmBlur = (e) => {
    const { value } = e.target;
    setPasswordState({
      ...passwordState,
      confirmDirty: passwordState.confirmDirty || !!value,
    });
  };

  const compareToFirstPassword = (rule, value, callback) => {
    if (value && value !== getFieldValue("password")) {
      callback(notEqualPasswords);
    } else {
      callback();
    }
  };

  const validateToNextPassword = (rule, value, callback) => {
    if (value && passwordState.confirmDirty) {
      validateFields(["confirmPassword"], { force: true });
    }
    callback();
  };

  return (
    <>
      <Form.Item>
        {getFieldDecorator("email", {
          rules: [
            {
              required: true,
              message: requiredValidator,
            },
            {
              type: "email",
              message: emailNotValid,
            },
            {
              max: 254,
              message: maximum,
            },
          ],
        })(<FormInputForAuth placeholder={email} />)}
      </Form.Item>
      <Form.Item>
        {getFieldDecorator("name", {
          rules: [
            {
              required: true,
              message: requiredValidator,
            },
            {
              max: 114,
              message: maximum,
            },
            {
              pattern: /^[\w.@+\-\s--]+$/,
              message: unacceptable,
            },
            {
              pattern: /^\S+\s\S+$/,
              message: fullnameValidation,
            },
          ],
        })(<FormInputForAuth placeholder={username} />)}
      </Form.Item>
      <Form.Item>
        {getFieldDecorator("password", {
          rules: [
            {
              required: true,
              message: requiredValidator,
            },
            {
              validator: validateToNextPassword,
            },
            {
              min: 8,
              message: notLessValidator,
            },
            {
              pattern: /^(?!^\d+$)^.+$/,
              message: onlyDigits,
            },
          ],
        })(<FormInputForAuth type="password" placeholder={password} />)}
      </Form.Item>
      <Form.Item>
        {getFieldDecorator("confirmPassword", {
          rules: [
            {
              required: true,
              message: requiredValidator,
            },
            {
              validator: compareToFirstPassword,
            },
          ],
        })(
          <FormInputForAuth
            type="password"
            placeholder={confirmPassword}
            onBlur={handleConfirmBlur}
          />
        )}
      </Form.Item>
    </>
  );
};

export default withNamespaces()(RegistrationForm);
