import React from "react";
import styled from "styled-components";
import { Row, Col, Typography } from "antd";
import { useLocation } from "react-router-dom";
// import * as R from "ramda";
import { withNamespaces } from "react-i18next";

import RegistrationForm from "./components/ToggleForm";
import { ReactComponent as SettingsIcon } from "../../assets/settings.svg";
// import { ReactComponent as FacebookIcon } from "../../assets/facebook.svg";
// import { ReactComponent as GoogleIcon } from "../../assets/google.svg";
import LinkLogo from "../../assets/link.png";
import RetinaLinkLogo from "../../assets/link@2x.png";
import Logo from "../../components/Logo/ResponsiveLogo";
// import axiosInstance from "../../axios/axiosInstance";
import ResetPasswordForm from "./components/ResetPasswordForm";

const { Text } = Typography;

const Registration = ({ t }) => {
  const { hash } = useLocation();

  const {
    title,
    subtitle1,
    subtitle2,
    rights,
    explainText,
    passwordRecovery,
  } = t("auth", {
    returnObjects: true,
  });

  return (
    <>
      <PageWrapper>
        <ColoredColumn span={12}>
          <Col offset={4}>
            <LogoWithLink
              src={LinkLogo}
              srcSet={`${LinkLogo},
            ${RetinaLinkLogo} 2x`}
              alt="logo"
            />
          </Col>
          <MainContent>
            <Row>
              {hash.includes("password/reset/confirm") ? (
                <>
                  <RegisterTitle
                    style={{ textAlign: "left", margin: "0 0 20px 10px" }}
                  >
                    {passwordRecovery}
                  </RegisterTitle>
                  <ResetPasswordForm hash={hash} />
                </>
              ) : (
                <FormCol span={24}>
                  <RegisterTitle>{title}</RegisterTitle>
                  <Col span={17}>
                    <SubTitle>
                      {subtitle1}
                      <br />
                      {subtitle2}
                      <MainLink href="/">River.</MainLink>
                    </SubTitle>
                  </Col>
                  <RegistrationForm />
                </FormCol>
              )}

              {/* <LittleText>   </LittleText>
                <Row gutter={30}>
                  <Col span={12}>
                    <a href="/">
                      <FacebookIcon />
                    </a>
                  </Col>
                  <Col span={12}>
                    <a href="/">
                      <GoogleIcon />
                    </a>
                  </Col>
                </Row> */}
            </Row>
          </MainContent>
          <FooterText>River 2019 {rights}</FooterText>
        </ColoredColumn>
        <Column span={12}>
          <Row gutter={[0, 20]}>
            <Col offset={2} span={22}>
              <SettingsIcon style={{ padding: 4 }} />
            </Col>
            <Col offset={6} span={14}>
              <StrictColumnText>{explainText}</StrictColumnText>
            </Col>
          </Row>
        </Column>
      </PageWrapper>
      <Logo />
    </>
  );
};

const LogoWithLink = styled.img`
  margin-top: 30px;
  max-width: 65px;
  font-weight: 200;
`;

const StrictColumnText = styled(Text)`
  color: #3b3b3b;
  font-size: 14px;
  line-height: 20px;
`;

const MainText = styled.p`
  color: #fff;
`;

const LittleText = styled(MainText)`
  font-weight: 200;
  font-size: 16px;
  line-height: 20px;
`;

const Column = styled(Col)`
  text-align: center;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const ColoredColumn = styled(Col)`
  background: linear-gradient(180deg, #f85c71 0%, #1d4e89 100%);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
`;

const MainContent = styled.div`
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
`;

const FormCol = styled(Col)`
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
`;

const MainLink = styled.a`
  color: #0056be;
`;

const RegisterTitle = styled.h2`
  color: #fff;
  font-weight: 800;
  font-size: 22px;
  letter-spacing: 0.4px;
`;

const PageWrapper = styled(Row)`
  width: 100vw;
  overflow: hidden;
  height: 100vh;
`;

const FooterText = styled(LittleText)`
  font-size: 12px;
  color: rgba(255, 255, 255, 0.36);
  text-align: center;
  letter-spacing: 0.5px;
  margin-bottom: 25px;
`;

const SubTitle = styled(MainText)`
  font-size: 15px;
  line-height: 34px;
  font-weight: 200;
  margin: 10px 0 40px 0;
`;

export default withNamespaces()(Registration);
